
* {
  padding: 0;
  margin: 0;
  -webkit-tap-highlight-color: transparent;
  -webkit-appearance: none;
  -webkit-overflow-scrolling: touch;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  word-break: break-all;
}
*::-webkit-scrollbar {
  display: none;
}
html {
  width: 100%;
  margin: 0 auto;
  overflow-x: hidden;
  height: 100%;
}
body {
  -webkit-font-smoothing: antialiased;
  background: #f5f5f5;
  width: 100%;
  height: 100%;
}
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td,
hr,
button,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
input[placeholder] {
  text-overflow: ellipsis;
}

.DragUpload {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #eee;
}
.DragUpload > div {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 600px;
  height: 200px;
}
.DragUpload .fileInput {
  float: left;
  position: relative;
  width: 300px;
  height: 200px;
  border: 1px dashed #666;
  border-radius: 5px;
  text-align: center;
  line-height: 200px;
  font-size: 20px;
  color: #666;
}
.DragUpload .fileInput > input {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  cursor: pointer;
}
.DragUpload .fileInput a {
  font-size: 16px;
  color: #333;
  width: 50%;
  height: 50px;
}
.DragUpload .fileInput .file {
  position: absolute;
  right: 25%;
  top: 100%;
}
.DragUpload .explan {
  float: left;
  width: 300px;
  height: 200px;
}
.DragUpload .explan img {
  height: 100%;
}
.shade {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 2;
}
.shade > img {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
}

#popupBox {
  width: 400px;
  height: 200px;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 4;
  margin: auto;
  border: 1px solid #ddd;
  border-radius: 5px;
  background: #ddd;
  text-align: center;
}
#popupBox > .closeBtn {
  position: absolute;
  right: 0;
  top: 0;
  width: 50px;
  height: 49px;
  text-align: center;
  line-height: 49px;
  border: 0;
  font-size: 18px;
  cursor: pointer;
}
#popupBox > .topBox {
  width: 100%;
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ccc;
  font-size: 18px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#popupBox > .conBox {
  width: 100%;
  height: 100px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 20px 0;
}
#popupBox > .bottomBox {
  width: 100%;
  height: 50px;
  border-top: 1px solid #ccc;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#popupBox > .bottomBox > button {
  position: absolute;
  right: 10px;
  bottom: 5px;
  width: 60px;
  height: 35px;
  background: #ccc;
  border-radius: 5px;
  text-align: center;
  line-height: 35px;
  border: 0;
  font-size: 15px;
  color: #fff;
  cursor: pointer;
}

#listBox {
  width: 100%;
  min-height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.6);
  text-align: center;
}
#listBox > .conBox {
  width: 100%;
  height: auto;
  color: #fff;
  padding: 20px 0;
}
#listBox > .conBox .title {
  color: #ccc;
}
#listBox > .conBox .green {
  color: #468847;
}
#listBox > .conBox .red {
  color: #b94a48;
}
#listBox > .conBox .blue {
  color: #3a87ad;
}
#listBox > .conBox li {
  padding: 5px 0;
}
#listBox > .conBox table {
  width: 100%;
  table-layout: fixed;
}
#listBox > .conBox table tr {
  height: 30px;
}
#listBox > button {
  width: 50px;
  height: 30px;
  background: #ddd;
  border-radius: 5px;
  text-align: center;
  line-height: 30px;
  border: 0;
  cursor: pointer;
}
